.contact-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px;
    border: 4px solid black;
    width: 50%;
    margin: 0 auto;
    margin-top: 140px;
    background-color: #fff;
}

.contact-container > span {
    font-size: 20px;
    margin-bottom: 50px;
    text-align: center;
    background-color: #fff;
    
}

.contact-container > a {
    color: black;
}

.contact-btn > i {
    font-size: 40px;
    background-color: #fff;

}
